//form đăng nhập

.nav2 {
    &_ul {
        display: flex;
        align-items: center;
    }

    &_li {
        list-style: none;
        margin-left: 20px;
        cursor: pointer;

        &__p {
            font-weight: bold;

            &:hover {
                color: #ee4d2d;
            }
        }

        &__logo {
            width: 30px;
            height: 30px;
        }
    }

    &_item1 {
        display: flex;
        align-items: center;
        background-color: #f6f6f6;
        border-radius: 2rem;
        padding: 4px 8px;

        &__p {
            margin: 0 6px;
        }
    }

    &_item2 {
        position: absolute;
        display: flex;
        justify-content: space-between;
        width: 450px;
        margin-left: -358px;
        background-color: #fff;
        border: 1px solid #e7e7e7;
        box-shadow: 0 2px 8px 0 #e7e7e7;
        top: 86px;
        animation: model 0.5s linear;
        z-index: 1;

        &::before {
            content: '';
            border-color: transparent transparent #e7e7e7 transparent;
            border-width: 11px 14px;
            border-style: solid;
            position: absolute;
            top: -22px;
            right: 16px;
        }
    }

    &_form {
        position: fixed;
        width: 400px;
        top: 50%;
        left: 36%;
        transform: translateY(-50%);
        padding: 0 32px;
        background-color: #fff;
        border-radius: 5px;
        z-index: 5;
    }
}
.auth {
    &_input {
        width: 100%;
        height: 40px;
        margin-top: 16px;
        padding-left: 18px;
        box-sizing: border-box;
        border: 1px solid #dbdbdb;
        border-radius: 4px;
        outline: none;
        font-size: 16px;
    }

    &_froup {
        &__pass {
            position: relative;
            display: flex;
        }

        &__eye {
            position: absolute;
            right: 6%;
            top: 50%;
            color: #666;
            cursor: pointer;
        }
    }
}

.form {
    &_header {
        display: flex;
        justify-content: space-between;
        margin: 32px 0;
        padding: 0 12px;
    }

    &_heading {
        font-weight: 600;
        font-size: 20px !important;
    }

    &_switch {
        color: #ee4d2d;
        font-size: 18px;
    }

    &_controls {
        display: flex;
        justify-content: flex-end;
        margin: 80px 0;
    }

    &_controls1 {
        margin-bottom: 20px;
    }

    &_controls2 {
        display: flex;
        justify-content: flex-end;
        margin: 20px 10px 70px;

        &_p {
            margin-right: 20px;
        }

        p {
            color: blue;

            &:hover {
                color: #ee4d2d;
            }
        }
    }

    &_btn {
        min-width: 124px;
        height: 40px;
        outline: none;
        border: none;
        background-color: #ccc;
        border-radius: 4px;
        cursor: pointer;

        &:hover {
            background-color: #555;
            color: #fff;
        }
    }

    &_primary {
        margin-left: 10px;
        color: #fff;
        background-color: #ee4d2d;

        &:hover {
            background-color: #ee6549d3;
        }
    }
}

.model {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.4);
    animation: model 0.5s linear;
    z-index: 4;

    &_search {
        background: rgba(0, 0, 0, 0.01);
        z-index: 2;
    }
}

@keyframes model {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.form_message {
    margin-left: 20px;
    letter-spacing: 1px;
    margin-top: 10px;
    color: #eb1336;
}

.controls2 {
    &_p {
        margin-right: 20px;
    }
}
