// table,
.product {
    // height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: #f62d3e;
    margin: 60px 0;

    &_img {
        width: 200px;
        display: flex;
        margin: auto;
    }
}

.name {
    width: 400px;
}

tr {
    background-color: #efefef;
    border: 1px solid #ddd;
    &:hover {
        background-color: #ddd;
    }
}

table,
tr,
th,
td {
    // border: 1px solid #ddd;
    border-collapse: collapse;
    padding: 8px;
}

th {
    background-color: #ddd;
    text-align: left;
}

td {
    padding-left: 12px;
}

.cart {
    margin: 60px 0;
}

.table {
    position: relative;
    width: 1170px;
    margin: auto;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

.image {
    width: 50px;
    height: 50px;
    border-radius: 4px;
}

.del {
    p {
        display: inline;

        &:hover {
            color: #f62d3e;
            cursor: pointer;
        }
    }
}

.active {
    display: none;
}

.total {
    position: absolute;
    right: 4%;
    padding-top: 20px;
}

@media screen and (min-width: 768px) {
    .table {
        width: auto;
    }
    .quantity {
        display: none;
    }
}

@media screen and (max-width: 768px) {
    .table {
        width: auto;
    }
    .price {
        display: none;
    }
}

@media screen and (max-width: 576px) {
    .table {
        margin: 60px 20px;
    }

    th {
        font-size: 14px;
    }

    td {
        font-size: 12px;
    }

    .product {
        img {
            width: 100px;
        }

        p {
            font-size: 14px;
        }
    }
}
